.footerBlock {
    background-color: #f5f5f5;
    display: flex;
    border: 1px solid red;
}

.footerContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 80px 0;
    width: 100%;
    border: 1px solid red;
}

.footerContainer h2 {
    font-family: 'Futura PT';
    color: #323232;
    text-transform: capitalize;
    font-size: 22px;
    letter-spacing: 5px;
    border: 1px solid red;
}


.socialBlock {
    width: 100%;
    border: 1px solid red
}

.footer {
    display: flex;
    padding: 20px 0;
    justify-content: center;
    gap: 20px;
}

.footer li{
    border: 1px solid #c7c4c4;
    border-radius: 50%;
    color: #687693;
    font-size: 15px;
    height: 40px;
    line-height: 42px;
    text-align: center;
    width: 40px;
    cursor: pointer;
}

.socialIcon {
    font-size: 15px;
    color: #687693;
}

.footerContainer span {
    color: #687693;
    font-size: 18px;
    line-height: 1.5em;
    margin-bottom: 0;
    font-family: 'Josefin Sans', sans-serif;
}