.navBlock {
  height: 100%;
}

.navContainer {
    width: 300px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    position: relative;
}

.active {
    text-decoration: none;
    color: #323232;
    cursor: pointer;
    font-family: Futura PT;
    font-size: 22px;
    font-weight: 500;
}

.active:hover {
    color: #E5540A;
}

