
.contactsBlock {
    background-color: #ffffff;
    border: 1px solid red;
}

.contactsContainer {
    flex-direction: column;
    justify-content: flex-start;
    border: 1px solid red;
}

.title {
    border: 1px solid red;
    margin-bottom: 50px;
    font-family: 'Futura PT';
    color: #323232;
    text-transform: capitalize;
    font-size: 26px;
    letter-spacing: 5px;
    text-align: center;
}


.form {
    border: 1px solid red;
    background-color: #f5f5f5;
    display: flex;
    width: 90%;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 35px 0;
}


.inputContainer {
    border: 1px solid red;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 0 20px
}

.spanElement {
    border: 1px solid red;
    padding: 10px 0;
    font-size: 15px;
    font-weight: 400;
    line-height: 160%;
    font-family: Josefin Sans, sans-serif;
}


.inputContainer input {
    background: transparent;
    border: 1px solid hsla(225, 8%, 62%, .48);
    box-shadow: none !important;
    height: 56px;
    padding-left: 25px;
    font-family: Josefin Sans, sans-serif;
}

.inputContainer input:focus {
    background: #ffffff;
    border: 1px solid #E5540A;
    outline: none !important;
    box-shadow: none !important;
    height: 56px;
    padding-left: 25px;
}

.blockTextArea {
    border: 1px solid red;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0 20px;
}

.formTextArea {
    border: 1px solid red;
    height: 107px;
    padding-top: 15px;
    background: transparent;
    border: 1px solid hsla(225, 8%, 62%, .48);
    box-shadow: none !important;
    padding-left: 25px;
}

.formTextArea:focus {
    background: #ffffff;
    border: 1px solid #E5540A;
    border-radius: 0;
    outline: none !important;
    box-shadow: none !important;
    padding-left: 25px;
    width: 98%;
    height: 107px;
    padding-top: 15px;
}

.blockButtonAndContacts {
    border: 1px solid red;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-grow: 1;
    height: 100%;
    flex-wrap: wrap-reverse;
    padding: 25px 20px;
    gap: 25px
}


.submitButton {
    border: 1px solid red;
    background: #19181d;
    border-radius: 0;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    font-weight: 600;
    padding: 15px 45px;
    position: relative;
    text-transform: capitalize;
    transition: all .3s;
    font-family: Josefin Sans, sans-serif;
}

.submitButton:hover {
    background: #E5540A;
}

.containerPersonalInformation {
    border: 1px solid red;
    display: flex;
    align-items: center;
}

.socialIconContainer {
    background: rgba(229, 84, 10, .2);
    border: 1px solid #e5540a;
    border-radius: 50%;
    color: #198754;
    font-size: 14px;
    height: 29px;
    line-height: 29px;
    margin-right: 5px;
    text-align: center;
    width: 29px;
    margin-left: 10px;
}

.containerPersonalInformation span {
    font-family: Josefin Sans, sans-serif;
    color: #687693;
    height: 100%;
    text-align: center;
    font-size: 14px;
}

.fontIcon {
    color: #E5540A;
    font-size: 14px;
    opacity: 70%;
}


