
.mainBlock {
    height: 100%;
    background-color: #ffffff;
    display: flex;
}

.mainContainer {
    border: 1px solid red;
}

.main {
    border: 1px solid red;
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    align-items: center;
    gap: 15px;
}

.socialContainer {
    border: 1px solid red;
}

.socialIcon li {
    padding: 10px 0 10px 0;
    cursor: pointer;
    position: relative;
}

.socialIcon a {
    border: 1px solid red;
    writing-mode: vertical-lr;
    transform: rotate(-180deg);


    color: #323232;
    text-decoration: none;
    font-family: Josefin Sans, sans-serif;
    font-size: 16px;
}


.socialIcon a:hover {
    color: #e5540a;
}

.profile {
    display: flex;
    border: 1px solid red;
    gap: 10px;
    flex-grow: 1;
    align-items: center;
}

.textContainer {
    border: 1px solid red;
    flex-grow: 1;
}

.text {
    border: 1px solid red;
    width: 80%;
    background-color: #ffffff;
}

.text span {
    display: inline-block;
    font-size: 20px;
    letter-spacing: 5px;
    position: relative;
    text-transform: capitalize;
}

.text p {
    display: inline-block;
    font-size: 20px;
    letter-spacing: 5px;
    padding-right: 120px;
    position: relative;
    text-transform: capitalize;
}

.text h1 {
    font-size: 50px;
    line-height: 80px;
    color: #323232;
    font-weight: 900;
    font-family: Futura PT;
}


.photoContainer {
    border: 1px solid red;
    flex-grow: 1;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.profilePhoto {
    height: 300px;
    display: flex;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    justify-content: center;
    align-items: center;
    position: relative;
}

@media screen and (max-width: 960px) {
    .profile {
        flex-direction: column;
    }
    .text {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}
